import { FC } from 'react';

import ashape1 from '../assets/images/ashape1.png';
import ashape2 from '../assets/images/ashape2.png';
import ashape3 from '../assets/images/ashape3.png';
import ashape4 from '../assets/images/ashape4.png';
import binarycalc from '../assets/images/binarycalc.png';
import htimg6 from '../assets/images/ht-img6.png';
import htimg7 from '../assets/images/ht-img7.png';
import htimg8 from '../assets/images/ht-img8.png';

const About: FC = () =>(
    <section id="calculadora" className="fea-area about-area about-area3 padding-top-120 padding-bottom-90">
        <div className="about-shapes">
            <img src={ashape1} alt="" className="as1 item-animateTwo" />
            <img src={ashape2} alt="" className="as2 item-bounce" />
            <img src={ashape3} alt="" className="as3" />
            <img src={ashape4} alt="" className="as4 item-animateTwo" />
        </div>
        <div className="container">
            <div className="row align-items-center">
                <div className="col-lg-6 col-md-12 margin-bottom-30 wow fadeInLeft">
                    <div className="fea-left">
                        <img src={binarycalc} style={{ maxWidth: '100%' }} alt="" />
                    </div>
                </div>
                <div className="col-lg-6 col-md-12 wow fadeInRight">
                    <div className="fea-right">
                        <div className="common-title">
                            <h3>Calculadora Binária</h3>
                            <p>Com esta calculadora você poderá somar, restar, multiplicar e dividir inserindo somente numeros binários.
                                O resultado será o equivalente binário do cálculo.
                            </p>
                            <div className="fea-box">
                                <ul>
                                    <li><span><i className="fas fa-angle-double-right"></i></span> Soma</li>
                                    <li><span><i className="fas fa-angle-double-right"></i></span> Resta</li>
                                    <li><span><i className="fas fa-angle-double-right"></i></span> Multiplica</li>
                                    <li><span><i className="fas fa-angle-double-right"></i></span> Divide</li>
                                </ul>
                            </div>
                            <a target='_blank' rel='nonreferrer' href="https://play.google.com/store/apps/details?id=com.techgeek.binarycalculator" className="template-btn3">Baixa Já</a>
                            <a data-wow-delay=".3s" href="#politica" style={{ marginLeft: '5%' }} className="template-btn4 wow fadeInLeft">politica de privacidade</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
)

export default About;