import { FC } from 'react';
import hshape6 from '../assets/images/h-shape6.png';
import htimg1 from '../assets/images/ht-img1.png';
import htimg2 from '../assets/images/ht-img2.png';
import htimg3 from '../assets/images/ht-img3.png';
import htimg4 from '../assets/images/ht-img4.png';
import htshape1 from '../assets/images/ht-shape1.png';
import htshape3 from '../assets/images/ht-shape3.png';
import htshape4 from '../assets/images/ht-shape4.png';
import htshape5 from '../assets/images/ht-shape5.png';
import htshape6 from '../assets/images/ht-shape6.png';
import htshape7 from '../assets/images/ht-shape7.png';

const Header: FC = () =>(
    <div className="hero3-slider-active">
        <div className="hero-section3">
            <div className="hero3-shapes">
                <img className="hts1 item-bounce" src={htshape4} alt="" />
                <img className="hts2 item-animateTwo" src={htshape5} alt="" />
                <img className="hts3 item-animateOne" src={htshape6} alt="" />
            </div>
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-lg-6">
                        <div className="hero3-left">
                            <span className="heading1  wow fadeInLeft">novas apps</span>
                            <span className="heading2  wow fadeInLeft" data-wow-delay=".2s">Vamos facilitar sua vida com nossas apps </span>
                            <a data-wow-delay=".3s" href="#calculadora" className="template-btn3 wow fadeInLeft">teste nossas apps</a>
                        </div>
                    </div>
                    <div className="col-lg-6 d-none d-lg-block">
                        <div className="hero3-right">
                            <div className="htr-shapes">
                                <img className="htr1" src={htshape3} alt="" />
                                <img className="htr2" src={htimg2} alt="" />
                                <img className="htr3" src={htimg3} alt="" />
                                <img className="htr4" src={htimg4} alt="" />
                                <img className="htr5 item-zooming" src={hshape6} alt="" />
                                <img className="htr6" src={htshape1} alt="" />
                                <img className="htr7" src={htshape7} alt="" />
                            </div>
                            <img className="main-img" src={htimg1} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
)

export default Header;